import type {ActiveDescendantChangedEvent, RenderedEvent} from './virtual-list-element'
import type VirtualListElement from './virtual-list-element'

// Determines whether the focus has been applied or not yet
// in the current rendering cycle
const enum FocusCommitmentState {
  Moved,
  Committed,
}

/**
 * Focus management for a scrollable single-select listbox-like model, designed specifically to be compatible with virtual list implementation.
 * See https://www.w3.org/WAI/ARIA/apg/patterns/listbox/ and https://www.w3.org/WAI/ARIA/apg/example-index/listbox/listbox-scrollable.html
 *
 * Features:
 *  - Maintains focus capture state (when focus should not leave rootElement)
 */
export default class VirtualListboxFocusState<V> {
  constructor(rootElement: VirtualListElement<V>) {
    this.#rootElement = rootElement
    this.#itemsArray = Array.from(rootElement.values())
    this.#onReleaseFocus = this.releaseFocus.bind(this)
    this.#rootElement.addEventListener('focus', () => {
      // this three function calls are order-sensitive, thus this condition cannot be optimized
      if (!this.isExistingItemFocused()) {
        this.captureFocus()
        this.#rootElement.update()
      } else {
        this.captureFocus()
      }
    })
    this.#rootElement.addEventListener('virtual-list-rendered', e => this.handleRenderFocus(e as RenderedEvent<V>))
    this.#rootElement.addEventListener('virtual-list-data-updated', () => this.updateItems())
    this.#rootElement.addEventListener('virtual-list-activedescendant-changed', e =>
      this.handleFocusChange(e as ActiveDescendantChangedEvent),
    )
    this.#rootElement.addEventListener('keydown', e => this.handleKeydown(e))
    this.#rootElement.addEventListener('click', e => this.handleClick(e))
  }

  #rootElement: VirtualListElement<V>
  #itemsArray: V[]
  #focusedItem: V | undefined
  #focusedElement: HTMLElement | undefined
  #focusIsCaptured = false
  #commitmentState: FocusCommitmentState
  #onReleaseFocus: (this: HTMLElement, ev: FocusEvent) => void

  public isFocused() {
    return this.#focusIsCaptured
  }

  public updateItems() {
    this.#itemsArray = Array.from(this.#rootElement.values())
    if (this.isFocused() && !this.isExistingItemFocused()) {
      this.focusFirst()
    }
  }

  public focusFirst() {
    this.defocusCurrentElement()
    this.#commitmentState = FocusCommitmentState.Moved
    this.#focusedItem = this.#itemsArray[0]
  }

  public focusLast() {
    this.defocusCurrentElement()
    this.#commitmentState = FocusCommitmentState.Moved
    this.#focusedItem = this.#itemsArray[this.#itemsArray.length - 1]
  }

  public focusNext() {
    let nextFocusedItem = undefined

    if (this.#focusedItem) {
      const currentIndex = this.#itemsArray.indexOf(this.#focusedItem)
      if (currentIndex === this.#itemsArray.length - 1) {
        nextFocusedItem = this.#itemsArray[currentIndex]
      } else {
        nextFocusedItem = this.#itemsArray[currentIndex + 1]
      }
    } else {
      nextFocusedItem = this.#itemsArray[0]
    }

    this.defocusCurrentElement()
    this.#commitmentState = FocusCommitmentState.Moved
    this.#focusedItem = nextFocusedItem
  }

  public focusPrev() {
    let prevFocusedItem = undefined

    if (this.#focusedItem) {
      const currentIndex = this.#itemsArray.indexOf(this.#focusedItem)
      if (currentIndex === 0) {
        prevFocusedItem = this.#itemsArray[0]
      } else if (currentIndex === -1) {
        prevFocusedItem = this.#itemsArray[0]
      } else {
        prevFocusedItem = this.#itemsArray[currentIndex - 1]
      }
    } else {
      prevFocusedItem = this.#itemsArray[0]
    }

    this.defocusCurrentElement()
    this.#commitmentState = FocusCommitmentState.Moved
    this.#focusedItem = prevFocusedItem
  }

  public focusIndex(itemIndex: number) {
    this.defocusCurrentElement()

    if (itemIndex > -1 && itemIndex < this.#itemsArray.length) {
      this.#focusedItem = this.#itemsArray[itemIndex]
    } else {
      this.resetFocus()
    }
    this.#commitmentState = FocusCommitmentState.Moved
  }

  // capture focus state from rootElement and propagate it to the correct focusedElement
  public captureFocus() {
    this.markRootAsFocused()
    if (document.activeElement !== this.#rootElement) {
      // workaround for the case when internal item is focused explicitly
      // the focus should be moved to the list container
      this.#rootElement.focus()
    }
    if (!this.isExistingItemFocused()) {
      this.focusFirst()
    }
    this.#rootElement.addEventListener('focusout', this.#onReleaseFocus)
  }

  // remove focus from virtual list
  public releaseFocus() {
    this.markRootAsNotFocused()
    this.#rootElement.removeEventListener('focusout', this.#onReleaseFocus)
  }

  // reset the state of the listbox
  public resetFocus() {
    this.#focusedElement = undefined
    this.#focusedItem = undefined
    this.defocusCurrentElement()
    this.commitFocus()
  }

  // try capturing the HTML element for current focus state
  private captureFocusElement(renderCache: Map<V, Element>) {
    if (!this.#focusedItem) return

    const row = renderCache.get(this.#focusedItem)
    if (row) {
      const element = row.querySelector('[tabindex]') as HTMLElement
      this.setFocusElement(element)
    }
  }

  // Restore the focus inside rootElement after it was rerendered
  private restoreFocus() {
    if (this.isFocused()) {
      this.captureFocus()
      this.commitFocus()
    }
  }

  private setFocusElement(element: HTMLElement) {
    this.#focusedElement = element
  }

  private handleKeydown(event: Event) {
    if (!(event instanceof KeyboardEvent)) return

    //eslint-disable-next-line @github-ui/ui-commands/no-manual-shortcut-logic
    switch (event.key) {
      case 'ArrowDown':
        {
          if (this.isFocused()) {
            this.focusNext()
            this.#rootElement.update()
          }
          event.preventDefault()
          event.stopPropagation()
        }
        break
      case 'ArrowUp':
        {
          if (this.isFocused()) {
            this.focusPrev()
            this.#rootElement.update()
          }
          event.preventDefault()
          event.stopPropagation()
        }
        break
      case 'Home':
        {
          if (this.isFocused()) {
            this.focusFirst()
            this.#rootElement.update()
          }
        }
        break
      case 'End':
        {
          if (this.isFocused()) {
            this.focusLast()
            this.#rootElement.update()
          }
        }
        break
    }
  }

  private handleClick(e: MouseEvent) {
    const targetElement = e.target as HTMLElement
    if (this.#rootElement.list.contains(targetElement)) {
      const focusable = targetElement.closest('[tabindex]') as HTMLElement
      const posInSet = parseInt(targetElement.closest('[aria-posinset]')?.getAttribute('aria-posinset') || '')
      if (!focusable || isNaN(posInSet)) return

      this.#focusedElement = focusable
      this.focusIndex(posInSet - 1)
      this.#rootElement.update()
      this.captureFocus()
    }
  }

  private handleRenderFocus(e: RenderedEvent<V>) {
    // capture the correct row element for focusing
    // should be done after virtual list is stably rendered
    this.captureFocusElement(e.rowsCache)
    // restore or set focus in a newly rendered range
    this.restoreFocus()
  }

  private handleFocusChange(e: ActiveDescendantChangedEvent) {
    this.focusIndex(e.index)
  }

  private markRootAsNotFocused() {
    this.#focusIsCaptured = false
  }

  private markRootAsFocused() {
    this.#focusIsCaptured = true
  }

  private isExistingItemFocused() {
    return this.#focusedItem && this.#itemsArray.indexOf(this.#focusedItem) > -1
  }

  // apply visual focus and update relevant attributes in DOM when virtual list is rendered
  private commitFocus() {
    this.forceFocusOnElement()
    if (this.#focusedElement) {
      this.updateScrollPosition()
      this.#commitmentState = FocusCommitmentState.Committed
      this.#rootElement.setAttribute('aria-activedescendant', this.#focusedElement.id)
    }
  }

  private forceFocusOnElement() {
    for (const child of this.#rootElement.querySelectorAll('[tabindex]')) {
      child.classList.remove('focused')
    }
    const focusElement = this.#focusedElement
    if (!focusElement) return
    focusElement.classList.add('focused')
  }

  private defocusCurrentElement() {
    const focusElement = this.#focusedElement
    if (!focusElement) return

    focusElement.classList.remove('focused')
    this.#focusedElement = undefined
  }

  // racalculate the scroll position based on focused item visibility
  private updateScrollPosition() {
    // do not change scroll position if not focus item exists or the focus was already committed
    if (!this.#focusedItem || !this.#focusedElement || this.#commitmentState === FocusCommitmentState.Committed) return

    const focusedElement = this.#focusedElement
    const rootElement = this.#rootElement
    if (this.isItemDetached()) {
      this.scrollIntoView()
    } else if (this.isItemHiddenByTopBorder()) {
      rootElement.scrollTop = focusedElement.offsetTop
    } else if (this.isItemHiddenByBottomBorder()) {
      rootElement.scrollTop = focusedElement.offsetTop + focusedElement.offsetHeight - rootElement.clientHeight
    }
  }

  // update virtual list scroll position to make currently focused item visible
  private scrollIntoView() {
    if (!this.#focusedElement) return
    const offset = parseInt(this.#focusedElement.getAttribute('data-scrolltop') || '')
    if (!isNaN(offset)) {
      this.#rootElement.scrollTop = offset
    }
  }

  private isItemHiddenByTopBorder() {
    if (!this.#focusedElement || !this.#rootElement) return false
    return this.#focusedElement.offsetTop < this.#rootElement.scrollTop
  }

  private isItemHiddenByBottomBorder() {
    const rootElement = this.#rootElement
    const focusedElement = this.#focusedElement
    if (!focusedElement || !rootElement) return false

    const scrollBottom = rootElement.scrollTop + rootElement.clientHeight
    const elementBottom = focusedElement.offsetTop + focusedElement.offsetHeight
    return elementBottom > scrollBottom
  }

  private isItemDetached() {
    if (!this.#focusedElement) return false
    return !this.#focusedElement.offsetParent
  }
}
